body {
  font-size: 24px;
}

.wrapper {
  background-color: rgb(78, 71, 71);
  min-height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 50px;
  padding-bottom: 50px;
}

.todo__title {
  margin-bottom: 20px;
}

.todo__list {
  flex: 1 0 auto;
}

.todo__list li:not(:last-child) {
  margin-bottom: 20px;
}

.todo__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.todo__container {
  display: flex;
  gap: 5px;
}

.button {
  padding: 5px;
  min-width: 100px;
  border-radius: 20px;
  text-align: center;
  background-color: rgb(12, 131, 170);
}

.todo__form {
  display: flex;
  gap: 10px;
}

.todo__input {
  border-radius: 10px;
  text-align: center;
  min-height: 40px;
}

.button__add {
  font-size: 20px;
  padding: 20px;
}
